import React from "react";

// Props: linkClass, linkTitle, linkHref, cover, githubHref, projectObjective, results, process, outcome, projectQuote

class ProjectBox extends React.Component {
  state = {
    isOpen: false,
  };
  render() {
    return (
      <div
        className={
          "projectBox scrollRevealX " +
          this.props.linkClass +
          " " +
          (this.state.isOpen == true ? "openProjectBox" : "")
        }
      >
        <a
          className="projectCoverLink"
          title={this.props.linkTitle}
          href={this.props.linkHref}
          target="_blank"
        >
          <span className="btnFocus" tabIndex="-1">
            {this.props.cover}
            <button
              className="projectViewBtn"
              title=""
              onClick={(event) => {
                // Prevents any "default" action/behavior
                // Default actions come automatically from our HTML (i.e. <a> tag click actions)
                event.preventDefault();
                this.setState({
                  isOpen: !this.state.isOpen,
                });
              }}
            >
              <span className="btnFocus" tabIndex="-1">
                <i className="fas fa-angle-down projectArrowDown"></i>
              </span>
            </button>
          </span>
        </a>

        <div className="projectContent">
          <a
            tabIndex={this.state.isOpen == true ? undefined : -1}
            title={this.props.linkTitle}
            className="projectVisitLink projectLink"
            target="_blank"
            href={this.props.linkHref}
          >
            Visit <i className="fas fa-share"></i>
          </a>
          <a
            tabIndex={this.state.isOpen == true ? undefined : -1}
            title="GitHub"
            className="projectGithubLink projectLink"
            target="_blank"
            href={this.props.githubHref}
          >
            &lt;View Code/&gt;
          </a>
          <div className="projectText">
            <span className="overviewSpan">Objective:</span>&nbsp;
            {this.props.projectObjective}
          </div>

          {this.props.results}

          <div className="projectText">
            <span className="overviewSpan">Process:</span>&nbsp;
            {this.props.process}
          </div>

          {this.props.outcome}

          {this.props.projectQuote}
        </div>
      </div>
    );
  }
}

export default ProjectBox;
